import React, { useState } from 'react';
import './style.css';

export default function PaginaInicial(){
      //[estado         ,função p/ mudar nº ]
  const [numeroAleatorio, setNumeroAleatorio] = useState(1);

  function GerarNumero(){
    const novoNumero = Math.floor(Math.random() * (100-1) + 1); //Math.floor - arredonda para um número menor;       Math.random - define um número aleatório dentro de um intervalo.

    setNumeroAleatorio(novoNumero);
  }

  return (
    <div className="conteudo-centralizado">
      <h3>Numero Aleatório</h3>
      <h1>{numeroAleatorio}</h1>

      <div className="area-botao">
        <label>Clique no botão abaixo para gerar um número aleatório</label>

        <button onClick={GerarNumero}>
          Gerar Número
        </button>
      </div>
    </div>
  )
}

